<template>
  <div class="wrapper">
    <TheHeader></TheHeader>
    <base-spinner v-if="$store.state.operationUnderProcess"></base-spinner>
    <router-view></router-view>
    <TheFooter></TheFooter>
  </div>
</template>

<script>
const { ethereum } = window;

import { mapGetters } from "vuex";
import TheHeader from "./components/layout/TheHeader.vue";
import TheFooter from "./components/layout/TheFooter.vue";

export default {
  components: { TheHeader, TheFooter },
  mounted() {
    ethereum.on("disconnect", () => {
      this.$router.push("/");
      alert("Connect Wallet to use KajuSwap!");
    });
    ethereum.on("chainChanged", (_chainId) => this.$router.go());
    ethereum.on("accountsChanged", (accounts) => {
      console.log("new Account ->", accounts);
      // this.onConnect();
      // if (this.$route.path === "/swap" || this.$route.path === "/pool") {
      // if (this.$route.path === "/swap") {
      //   //lines 28-39 are just being used because the app doesn't use the WebHash mode.
      //   this.$router.push("/swap");
      // } else {
      //   this.$router
      //     .push("/")
      //     .then(() => {
      //       this.$router.push("/pool");
      //     })
      //     .catch((err) => {
      //       console.log("error->>>", err);
      //     });
      // }
      this.$router.go(); //If Hash mode is used this can be used.
      // } else {
      //   this.$router.push("/");
      //   if (this.$route.path !== "/") {
      //     alert("You are getting redirected to the home page!");
      //   }
      // }
    });
  },
  methods: {
    walletConnectInitializations() {
      this.$store.dispatch("displayMaxTokenBalance", {
        add: this.swapDialogVars.DialnumAdd[0],
        ind: 0,
      });
      this.$store.dispatch("displayMaxTokenBalanceLiq", {
        add: this.liqDialogVal.DialnumAdd[0],
        ind: 0,
      });
      this.$store.dispatch("displayMaxTokenBalance", {
        add: this.swapDialogVars.DialnumAdd[1],
        ind: 1,
      });
      this.$store.dispatch("displayMaxTokenBalanceLiq", {
        add: this.liqDialogVal.DialnumAdd[1],
        ind: 1,
      });
      this.$store.dispatch("displayReservesSwap");
      this.$store.dispatch("displayReservesPool");
    },

    onConnect() {
      this.$store.dispatch("toggleOperationUnderProcess", {
        val: true,
        location: "AccChange",
      });
      this.$store
        .dispatch("onConnect")
        .then(() => {
          this.walletConnectInitializations();
        })
        .catch((err) => console.log(err))
        .then(() => {
          // console.log("ok");
          this.$store.dispatch("toggleOperationUnderProcess", {
            val: false,
            location: "AccChange",
          });
        });
    },
  },
  computed: {
    ...mapGetters({
      swapDialogVars: "getSwapDialog",
      liqDialogVal: "getLiqDialog",
    }),
  },
};
